/* UpdateProduct.css */
.container {
    max-width: 600px;
    margin: 50px auto;
    padding: 30px;
    background: #ffffff;
    box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.1);
    border-radius: 10px;
    text-align: center;
}

.heading {
    color: #333;
    margin-bottom: 20px;
    font-size: 28px;
}

.form {
    display: flex;
    flex-direction: column;
    text-align: left;
}

.label {
    font-size: 16px;
    font-weight: 600;
    margin-bottom: 5px;
    color: #555;
}

.input {
    width: 100%;
    padding: 10px;
    margin-bottom: 15px;
    border: 1px solid #ddd;
    border-radius: 5px;
    font-size: 14px;
    transition: all 0.3s ease-in-out;
}

.button {
    background-color: #28a745;
    color: white;
    padding: 12px;
    font-size: 16px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    transition: all 0.3s ease-in-out;
}

.button:hover {
    background-color: #218838;
}
