/* Products.css */
.container {
    margin: 50px;
    padding: 20px;
    background-color: #f9f9f9;
    border-radius: 8px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.header {
    text-align: center;
    color: #333;
    font-size: 32px;
    margin-bottom: 30px;
}

.search-container {
    display: flex;
    justify-content: center;
    margin-bottom: 20px;
}

.search-container input {
    width: 60%;
    padding: 10px;
    font-size: 16px;
    border: 1px solid #ccc;
    border-radius: 5px;
}

.search-container button {
    padding: 10px 15px;
    margin-left: 10px;
    background-color: #007bff;
    color: white;
    border: none;
    border-radius: 5px;
    font-size: 16px;
    cursor: pointer;
}

.search-container button:hover {
    background-color: #0056b3;
}

.no-results {
    text-align: center;
    font-size: 18px;
    color: red;
    margin-top: 20px;
}

.product-list {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 20px;
    margin-top: 30px;
}

.product-item {
    background-color: #fff;
    padding: 15px;
    border-radius: 8px;
    box-shadow: 0 2px 6px rgba(0, 0, 0, 0.1);
    text-align: center;
}

.product-item h3 {
    color: #333;
    font-size: 18px;
    margin-bottom: 10px;
}

.product-item p {
    color: #555;
    font-size: 14px;
    margin-bottom: 10px;
}

.product-item img {
    max-width: 100%;
    border-radius: 5px;
    margin-bottom: 10px;
}

.product-item button {
    padding: 8px 12px;
    background-color: #28a745;
    color: white;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    font-size: 14px;
}

.product-item button:hover {
    background-color: #218838;
}

.download-report-btn {
    display: block;
    margin: 30px auto;
    padding: 12px 20px;
    background-color: #17a2b8;
    color: white;
    border: none;
    border-radius: 5px;
    font-size: 16px;
    cursor: pointer;
}

.download-report-btn:hover {
    background-color: #138496;
}
