/* AddProduct.css */
.add-product-container {
    max-width: 600px;
    margin: 50px auto;
    padding: 30px;
    background: #ffffff;
    box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.1);
    border-radius: 10px;
    text-align: center;
}

.add-product-container h1 {
    color: #333;
    margin-bottom: 20px;
    font-size: 28px;
}

.add-product-form {
    display: flex;
    flex-direction: column;
}

.add-product-form label {
    font-size: 16px;
    font-weight: 600;
    margin-bottom: 5px;
    text-align: left;
    color: #555;
}

.add-product-form input {
    width: 100%;
    padding: 10px;
    margin-bottom: 15px;
    border: 1px solid #ddd;
    border-radius: 5px;
    font-size: 14px;
    transition: all 0.3s ease-in-out;
}

.add-product-form input:focus {
    border-color: #007bff;
    outline: none;
    box-shadow: 0px 0px 5px rgba(0, 123, 255, 0.5);
}

.add-product-form button {
    background-color: #007bff;
    color: white;
    padding: 12px;
    font-size: 16px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    transition: all 0.3s ease-in-out;
}

.add-product-form button:hover {
    background-color: #0056b3;
}

@media (max-width: 768px) {
    .add-product-container {
        width: 90%;
        padding: 20px;
    }
}
