
/* General navbar styling */
.navbar {
    background-color: #2f2f32; /* Dark Background */
    padding: 10px 1px;
    position: sticky;
    top: 0;
    z-index: 1000;
    display: flex;
    justify-content: center;
    align-items: center;
    box-shadow: 0 4px 8px rgba(23, 13, 13, 0.2);
  }
  
  /* Navbar Flexbox */
  .navbar-container {
    width: 100%;
    max-width: 1300px;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  
  /* Navigation Items */
  .nav-item {
    color: #fffefe;
    text-decoration: none;
    font-size: 20px;
    padding: 10px 20px;
    transition: 0.3s;
  }
  
  .nav-item:hover {
    color: #f2c781; /* Highlight Color */
  }
  
  /* Dropdown Button */
  .dropbtn {
    background: none;
    border: none;
    color: rgb(245, 245, 245);
    font-size: 17px;
    cursor: pointer;
    padding: 10px 20px;
    display: flex;
    align-items: center;
    gap: 5px;
  }
  
  /* Dropdown Content */
  .dropdown-content {
    display: flex; /* Flexbox for horizontal layout */
    position: absolute;
    background-color: rgb(251, 246, 246);
    min-width: 250px; /* Set a more compact width */
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
    border-radius: 5px;
    overflow: hidden;
    justify-content: space-between; /* Make it more compact horizontally */
    padding: 10px;
  }
  
  /* Product Category Styling */
  .dropdown-category {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    width: 200px; /* Set a fixed width for each category */
  }
  
  /* Dropdown Item */
  .dropdown-item {
    color: black;
    padding: 12px 10px;
    display: block;
    text-decoration: none;
    font-size: 19px;
    font-weight:bold
  }
  
  .dropdown-item:hover {
    background-color: #ecc07a;
    color: rgb(251, 251, 251);
  }
  
  /* Product Example Links */
  .product-examples {
    display: flex;
    flex-direction: column;
    gap: 5px;
    margin-top: 10px;
  }
  
  .product-examples a {
    font-size: 17px;
    color: #555;
    padding: 5px;
  }
  
  .product-examples a:hover {
    background-color: #d6b574;
    color: rgb(234, 232, 232);
  }
  
  /* Logo Container */
  .logo-container {
    display: flex;
    align-items: center;
    justify-content: center;
  }
  
  .logo {
    width: 80px;
    height: auto;
    border-radius: 50%;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
  }
  
  /* Profile Icon */
  .profile-icon {
    font-size: 40px;
    color: white;
    cursor: pointer;
    transition: 0.3s;
  }
  
  .profile-icon:hover {
    color: #e9b767;
  }
  
 /* Home UL and List Items - Horizontal Layout */
.home-ul {
  display: flex;
  justify-content: space-around;
  padding: 0;
  margin: 5px 0;
  list-style-type: none;
}

.home-ll {
  background-color: #f4f4f4;
  padding: 7px;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  width: 200px;  /* Adjust width as necessary */
  text-align: center;
  transition: transform 0.3s ease, background-color 0.3s ease;
}

.home-ll h1 {
  font-size: 1.2rem;
  color: #333;
  margin: 0;
}

/* Hover effect for list items */
.home-ll:hover {
  background-color: #e0e0e0;
  transform: translateY(-5px);
  cursor: pointer;
}

/* Optional styling for smoother look */
.home-ul .home-ll h1:hover {
  color: #b2a31b;
}


  /* Full Horizontal Line */
  .full-line {
    width: 100%;
    border: none;
    height: 1px;
    background-color: #060809;
    margin: 0;
    padding: 0;
  }
  